<template>
  <div class="profile">
    <Header />
    <p v-if="loading" class="loading">Loading...</p>
    <article v-if="profileData" class="article">
      <header class="article__header">
        <img :src="profileData.platformInfo.avatarUrl" alt="User profile avatar" class="article__header__avatar">
        <h2 class="article__header__title">{{ profileData.platformInfo.platformUserId }}</h2>
      </header>
      <section class="article__content">
        <img v-if="profileData.segments[1].metadata.imageUrl" :src="profileData.segments[1].metadata.imageUrl" alt="User legend" class="article__content__img">
        <ul class="article__content__stats">
          <li v-if="profileData.metadata.activeLegend" class="article__content__stats__group">
            <h3>Current Legend</h3>
            <p>{{ profileData.metadata.activeLegendName }}</p>
          </li>
          <li v-if="profileData.segments[0].stats.level.value" class="article__content__stats__group">
            <h3>Rank</h3>
            <p>{{ profileData.segments[0].stats.level.value }}</p>
          </li>
          <li v-if="profileData.segments[0].stats.kills.value" class="article__content__stats__group">
            <h3>Kills</h3>
            <p>{{ profileData.segments[0].stats.kills.value }}</p>
          </li>
          <li v-if="profileData.segments[0].stats.kills.value" class="article__content__stats__group">
            <img :src="profileData.segments[0].stats.rankScore.metadata.iconUrl" alt="Rank medal">
          </li>
        </ul>
        <router-link to="/" class="nav__link">Home</router-link>
      </section>

    </article>

    <div v-if="error" class="error">
      <h2>{{ error }}</h2>
      <router-link to="/" class="nav__link--error">Home</router-link>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import Header from "@/components/Header";

export default {
  name: "Profile",
  components: {
    Header
  },
  data() {
    return {
      loading: true,
      error: null,
      profileData: null
    }
  },
  async created() {
    document.body.className = "";

    try {
      const URL = `/api/v1/profile/${this.$route.params.platform}/${this.$route.params.gamertag}`;
      const res = await axios.get(URL);

      this.profileData = res.data.data;
      this.loading = false;
    } catch (err) {
      this.loading = false;
      console.log(err.response);
      this.error = err.response.data.message;
    }

  }

}
</script>

