<template>
  <section class="search">
      <h1 class="search__title">Track Player Stats</h1>
      <form v-on:submit.prevent="submitForm" class="form">
          <div class="form__group">
              <label class="form__label" for="platform">Platform</label>
              <select v-model="platform" class="form__select" name="platform" id="platform">
                  <option value="psn">Playstation</option>
                  <option value="xbl">Xbox</option>
                  <option value="origin">PC (Origin)</option>
              </select>
          </div>
          <div class="form__group">
              <label class="form__label" for="gamertag">Gamertag</label>
              <input v-model="gamertag" class="form__input" name="gamertag" type="text" id="gamertag" placeholder="Origin ID, Xbox gamertag, or PSN ID..." required>
          </div>
          <div class="form__group">
              <input class="form__submit" type="submit" value="Submit">
          </div>
      </form>
  </section>
</template>

<script>
export default {
    name: "Search",
    data() {
        return {
            platform: "origin",
            gamertag: ""
        }
    },
    methods: {
        submitForm() {
            if (!this.gamertag) {
                return;
            }

            this.$router.push(`/profile/${this.platform}/${this.gamertag}`);
        }
    }
}
</script>

